
/*Form Styles*/
form {
    position: relative;
}

label,
input,
textarea {
    display: block;
}
input, textarea {
    padding:10px;
}
textarea {
    width: 100%;
    border-radius: 10px;
}

input {
    padding: 10px 0;
    width: 100%;
    border-radius: 10px;
    border: 1px solid #979797;
}

input.form-button {
    width: auto;
    position: absolute;
    right: 0;
    bottom: -80px;
    padding: 10px 40px;
}

label {
    font-weight: bold;
    text-transform: uppercase;
    font-size: 0.75em;
    margin-bottom: .5em;
}

.wpforms-submit-container{
    button.wpforms-submit {
        background-color: $primary !important;
        color: $white !important;;
        border-radius: 38px !important;
        font-weight: bold !important;
        transition: all 0.2s ease;
        letter-spacing: .1em !important;
        padding: 12px 24px !important;



        &:hover {
            background-color: $secondary !important;;
        }
    }
}

/*Layout Styles*/
.page-banner {
    height: auto;
    background-color: $gray;
display: block;
height: auto;
padding: 80px 40px;
text-align: center;

    @media (min-width: 1200px){
        display: flex;
    flex: 1;
    flex-wrap: wrap;
    justify-content: center;
    justify-items: center;
      width: 100%;
      max-width: 1440px;
      height: 50vh;
       padding: auto 40px;
    }

    h1 {
       font-size: clamp(16px, 6vw, 48px);
       line-height: 1.4;
       margin: auto auto 10px auto;
       
    }
    p {
        line-height: 1.4;
        margin-bottom: 10px;
    }
    .post-edit-link {
        display: block;
        clear: left;
        padding: 12px 10px 12px 0;

            @media (min-width: 1200px){
            	text-align: center;
            }

    }
}

.bio-photo {
    width: 250px;
    height: 250px;
    border-radius: 50%;
}


.skip {
    display: none;
}
.button {
    border-radius: 25px;
    color: $white;
    text-transform: uppercase;
    text-decoration: none;
    background-color: $blue;
    padding: 15px 40px;
    display: inline-block;
    font-size: .8em;
    transition: all 0.2s ease;
    font-weight: bold;
    letter-spacing: .1em;

    &:hover {
        background-color: $secondary;
        color: $white;
    }

}

.pill-container {
    margin: 40px auto;

    @media (min-width: 768px){
    	margin: 40px 0;
    }
}

.pill {
    border-radius: 38px;
    color: $white;
    text-transform: uppercase;
    text-decoration: none;
    background-color: $primary;
    padding: 12px 24px;
    margin:   0 auto 10px auto;
    display: inline-block;
    width: auto;
    text-align: center;
    font-size: .8em;
    transition: all 0.2s ease;
    font-weight: bold;
    letter-spacing: .1em;

    @media (min-width: 576px){
        display: inline-block;

    }
    @media (min-width:768px) {
        padding: 18px 32px;
        width: auto;
        margin: 0 auto 10px auto;
        font-size: .8em;

    }

    @media (min-width: 992px) {

        font-size: .8em;
        padding: 12px 24px;
        margin: 0 10px 0 0;
    }

    &:hover {
        background-color: $secondary;
        color: $white;
    }
}

.button-alt {
    background-color: $secondary;

    &:hover {
        background-color: $primary;
    }
}
.grid-row {
    display: flex;
    display: block;
    width: 100%;

.grid-container {
    max-width: 1140px;
    justify-items: center;
    padding: 0 40px;
    @media (min-width: 768px){
        display: grid; 
        grid-template-rows: 1fr ;
        grid-template-columns: 1fr 1fr 1fr ;
        margin: auto;
        gap: 20px;
        padding: 0 40px;
    }
    .card { 
        border: 1px solid $primary; 
        padding: 20px; 
        margin-bottom: 20px;
        
        h3 {
            font-size: clamp(16px, 6vw, 28px);
                        line-height: 1.4;
        }
        img {
            width: 100%;
            height: auto;
            border-top-right-radius: 5px;
            border-top-left-radius: 5px; 
          
        }
        
        .entry-footer {
            font-size: clamp(14px, 3vw, 16px);
            line-height: 1.4;
            padding: 10px 0;
        }

    
    }
    
}
}
.mb-40 {
    margin-bottom: 40px;
}
.mb-80 {
    margin-bottom: 80px;
}