/*Resets*/
body {
  font-family: "Poppins", Arial, Helvetica, sans-serif;
}

html {
  box-sizing: border-box;
}

* {
  margin: 0;
  padding: 0;
  font-size: 16;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

.entry-footer {
  background-color: rgb(255, 255, 255);
}

footer {
  background-color: rgb(234, 234, 234);
  padding: 0;
  margin-top: auto;
}
footer p {
  margin-bottom: 0;
}

h1, h2, h3, h4, h5, h6 {
  display: block;
  color: rgb(2, 48, 71);
  margin-bottom: 10px;
}

h1 {
  font-size: clamp(16px, 6vw, 48px);
  line-height: 1.4;
}

h2 {
  font-size: clamp(16px, 6vw, 40px);
  line-height: 1.4;
}

h3 {
  font-size: clamp(16px, 6vw, 30px);
  line-height: 1.4;
}
h3 a {
  text-decoration: none;
}
h3 a:hover {
  text-decoration: underline;
}

h4 {
  font-size: clamp(16px, 6vw, 28px);
  line-height: 1.4;
}

h5 {
  font-size: clamp(16px, 6vw, 26px);
  line-height: 1.4;
}

h6 {
  font-size: clamp(16px, 6vw, 16px);
  line-height: 1.4;
}

main p {
  margin-bottom: 0.875em;
  line-height: 2rem;
  font-size: clamp(16px, 3vw, 16px);
  line-height: 1.8;
}

main h1 {
  margin-bottom: 20px;
}

section h1 {
  margin-bottom: 40px;
}

main a {
  color: #346aff;
  text-decoration: none;
  padding: 0 2px;
}
main a:hover {
  color: #023047;
  text-decoration: underline;
}

main ul li {
  padding: 5px 0;
}
main ul li a {
  color: #346aff;
}
main ul li a:hover {
  color: #023047;
}

.inline-pills {
  padding: 0;
  margin: 10px 10px 10px 0;
}
.inline-pills li {
  display: inline-block;
  padding: 12px 10px;
  border-radius: 10px;
  background-color: rgb(2, 48, 71);
  color: rgb(255, 255, 255);
  font-weight: bold;
  text-transform: uppercase;
  margin: 5px 0;
}
@media (min-width: 992px) {
  .inline-pills li {
    margin: 5px 10px 5px 0;
  }
}

table {
  margin: 0.3125em 0;
}
table th {
  background-color: rgb(234, 234, 234);
}
table th, table td {
  padding: 0.3125em;
  border: 1px solid rgb(234, 234, 234);
}

address {
  display: block;
  font-style: italic;
  line-height: 1.4rem;
  margin-bottom: 10px;
}

main ol, main ul {
  margin: 10px 12px;
}
main ol li li, main ul li li {
  padding-left: 14px;
}

main ol li {
  line-height: 1.8rem;
}
main ol li li {
  line-height: 1.8rem;
}

main ul.inline-list {
  padding-left: 0;
  margin-left: 0;
}
main ul.inline-list li {
  display: inline-block;
  padding: 6px 0;
  text-transform: uppercase;
  text-decoration: underline;
  margin: 5px 0;
}

code {
  color: crimson;
  background-color: #f1f1f1;
  padding: 2px;
  font-size: 105%;
}

pre {
  display: block;
  font-family: monospace;
  white-space: pre;
  margin: 2em 0;
  background-color: rgb(234, 234, 234);
  border-left: 5px solid rgb(2, 48, 71);
  position: relative;
  overflow: scroll;
  padding: 20px;
}

q {
  color: gray;
  font-style: italic;
}

dl {
  display: block;
  margin-top: 1em;
  margin-bottom: 1em;
  margin-left: 0;
  margin-right: 0;
  font-size: clamp(16px, 6vw, 16px);
  line-height: 1.6;
}

dt {
  display: block;
  background-color: rgb(234, 234, 234);
  padding: 10px;
  font-weight: bold;
  font-size: clamp(16px, 6vw, 16px);
  line-height: 1.6;
}

dd {
  padding: 10px 20px;
  font-size: clamp(16px, 6vw, 16px);
  line-height: 1.6;
}

blockquote {
  background-color: rgb(2, 48, 71);
  border-left: 5px solid #354a95;
  color: rgb(255, 255, 255);
  padding: 10px;
  font-size: clamp(16px, 6vw, 16px);
  line-height: 1.4;
}

cite {
  background-color: rgb(234, 234, 234);
  padding: 10px 20px;
  margin-left: 10px;
  font-style: italic;
}

.subheading {
  font-weight: 100;
  font-size: 1.2rem;
  line-height: 1.6rem;
  margin: 0 0 60px 0;
  padding: 0 10px;
}

.container {
  display: block;
  padding: 40px;
}
@media (min-width: 768px) {
  .container {
    display: flex;
    flex-wrap: wrap;
    padding: 60px 40px;
    margin: 0 auto;
    text-align: left;
    width: 100%;
    max-width: 1140px;
  }
}
@media (min-width: 992px) {
  .container {
    flex-wrap: unset;
  }
}

.wrap {
  flex-wrap: wrap;
}

.project-container {
  display: block;
  width: 100%;
  max-width: 1140px;
  padding: 0 40px 80px 40px;
}
@media (min-width: 1200px) {
  .project-container {
    display: flex;
    align-items: flex-start;
    margin: auto;
    flex-wrap: wrap;
    padding: 0 0 80px 0;
  }
}
.project-container .project {
  padding: 40px 0;
}
@media (min-width: 768px) {
  .project-container .project {
    padding: 40px 20px 0 20px;
  }
}
@media (min-width: 1200px) {
  .project-container .project {
    flex: 1 0 50%;
    margin-bottom: auto;
  }
}
.project-container .project img {
  width: 100%;
  height: auto;
  margin-bottom: 20px;
}

.post-heading img {
  width: 100%;
  height: 400px;
  margin: 30px 0 80px 0;
}

.post-container {
  padding: 40px 0;
  display: flex;
  flex-wrap: wrap;
  flex: 1;
  justify-content: center;
  align-items: center;
}

.post-content {
  width: 100%;
  max-width: 1440px;
  margin: 0 auto;
  display: block;
}

img {
  margin-bottom: 40px;
  width: 100%;
  height: auto;
}

@media (min-width: 768px) {
  .section-contents {
    display: flex;
    margin: auto;
  }
}
@media (min-width: 992px) {
  .section-contents {
    flex-wrap: unset;
    margin: 0 auto;
    width: 72%;
  }
}
.section-contents h1 {
  margin-bottom: 20px;
}
.section-contents .attachment-post-thumbnail {
  width: 100%;
  height: auto;
}
@media (min-width: 768px) {
  .section-contents .attachment-post-thumbnail {
    width: 100%;
  }
}
@media (min-width: 992px) {
  .section-contents .attachment-post-thumbnail {
    width: 100%;
  }
}
.section-contents .col {
  width: 100%;
}
@media (min-width: 768px) {
  .section-contents .col {
    width: 50%;
  }
}
@media (min-width: 992px) {
  .section-contents .col {
    width: 50%;
  }
}

.col {
  width: auto;
  display: inline-block;
  padding: 0 10px;
  vertical-align: top;
}

@media (min-width: 768px) {
  .col-50 {
    width: 50%;
  }
}
@media (min-width: 768px) {
  .col-75 {
    width: 75%;
  }
}
.row {
  padding: 40px 0 20px 0;
}
@media (min-width: 768px) {
  .row {
    padding: 40px 0;
  }
}
@media (min-width: 992px) {
  .row {
    padding: 80px 0 0 0;
  }
}

.flexstart {
  justify-content: flex-start;
}

.flexend {
  justify-content: flex-end;
}

.center {
  justify-content: center;
}

.spacearound {
  justify-content: space-around;
}

.spacebetween {
  justify-content: space-between;
}

.items-center {
  align-items: center;
}

.self-center {
  align-self: center;
}

.wrap {
  flex-wrap: wrap;
}

.text-center {
  text-align: center;
}

.grey-background {
  background-color: rgb(234, 234, 234);
  color: #333333;
}

.dark-background {
  background-color: rgb(2, 48, 71);
}

.blue-background {
  background-color: #354a95;
}

.orange-background {
  background-color: rgb(255, 183, 3);
}

.light-text {
  color: rgb(255, 255, 255);
}

.col-padding-20 {
  padding: 20px;
}

.col-spacing-5 {
  margin: 10px;
}

.marb-spacing-40 {
  margin: 0 auto 40px auto;
}

.mart-spacing-100 {
  margin-top: 100px;
}

.rounded-border {
  border-radius: 15px;
}

@media (min-width: 768px) {
  .padding-right-60 {
    padding-right: 60px;
  }
}
.section-spacing {
  padding: 40px;
}
@media (min-width: 768px) {
  .section-spacing {
    padding: 60px 0;
  }
}
@media (min-width: 992px) {
  .section-spacing {
    padding: 80px 0;
  }
}

.contact-section {
  display: block;
  text-align: center;
  padding: 40px 20px;
}
.contact-section h3 {
  margin-bottom: 20px;
  line-height: 1.4em;
}
@media (min-width: 768px) {
  .contact-section {
    padding: 40px 80px;
  }
}
@media (min-width: 1200px) {
  .contact-section {
    display: flex;
    flex-wrap: wrap;
    flex: 1;
    justify-content: center;
    align-items: center;
    height: 40vh;
    column-gap: 30px;
    text-align: left;
  }
}

footer.page-footer {
  display: flex;
  flex-wrap: wrap;
  height: 10vh;
  align-items: center;
  justify-content: center;
}

/*Layout Styles*/
.page-banner {
  height: auto;
  background-color: rgb(234, 234, 234);
  display: block;
  height: auto;
  padding: 80px 40px;
  text-align: center;
}
@media (min-width: 1200px) {
  .page-banner {
    display: flex;
    flex: 1;
    flex-wrap: wrap;
    justify-content: center;
    justify-items: center;
    width: 100%;
    max-width: 1440px;
    height: 50vh;
    padding: auto 40px;
  }
}
.page-banner h1 {
  font-size: clamp(16px, 6vw, 48px);
  line-height: 1.4;
  margin: auto auto 10px auto;
}
.page-banner p {
  line-height: 1.4;
  margin-bottom: 10px;
}
.page-banner .post-edit-link {
  display: block;
  clear: left;
  padding: 12px 10px 12px 0;
}
@media (min-width: 1200px) {
  .page-banner .post-edit-link {
    text-align: center;
  }
}

.bio-photo {
  width: 250px;
  height: 250px;
  border-radius: 50%;
}

.navigation {
  width: 100%;
  margin: auto;
  max-width: 1440px;
}
.navigation .nav-links {
  display: inline-flex;
  flex-wrap: unset;
  min-width: 100%;
}
.navigation .nav-links .nav-next, .navigation .nav-links .nav-previous {
  margin: auto;
  justify-content: space-between;
  align-items: center;
  padding: 0;
}
.navigation .nav-links .nav-next a, .navigation .nav-links .nav-previous a {
  padding: 6px 0;
  font-size: clamp(16px, 5vw, 24px);
  line-height: 1.4;
  font-size: clamp(16px, 6vw, 23px);
  line-height: 1.4;
  color: rgb(255, 255, 255);
}
.navigation .nav-links .nav-next a:hover, .navigation .nav-links .nav-previous a:hover {
  color: rgb(255, 255, 255);
}

#mainHeader {
  z-index: 1;
}

.nav-container {
  background-color: #ffffff;
  padding: 10px 5px;
}
.nav-container .container {
  max-width: 1140px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 20px;
}
.nav-container a {
  margin: 5px 0;
}
@media (min-width: 768px) {
  .nav-container a {
    margin: auto 0;
  }
}
.nav-container a.logo {
  color: rgb(2, 48, 71);
  text-decoration: none;
  text-transform: uppercase;
  font-weight: 700;
  line-height: 0;
  display: flex;
  align-items: center;
  font-size: 1.2rem;
  column-gap: 15px;
}
.nav-container img.bio-photo {
  max-width: 44px;
  height: auto;
  margin: 0 auto;
  display: none;
}
@media (min-width: 768px) {
  .nav-container img.bio-photo {
    display: flex;
  }
}
.nav-container button {
  display: inline-block;
  width: auto;
  align-self: flex-end;
  margin: auto 0;
  padding: 0;
  background-color: #ffffff;
  border: none;
  cursor: pointer;
}

.open-menu {
  font-size: 1.2rem;
  margin: auto;
  line-height: 0;
  color: rgb(2, 48, 71);
  font-size: 1.6em;
}

.sticky {
  position: fixed;
  top: 0;
  width: 100%;
}

.sticky + .content {
  padding-top: 102px;
}

.menu-width {
  width: 100%;
}
@media (min-width: 992px) {
  .menu-width {
    width: 100%;
  }
}

.main-menu {
  height: 100%;
  width: 0;
  position: fixed;
  z-index: 99999999999999;
  top: 0;
  right: 0;
  background-color: #f9f9f9;
  overflow-x: hidden;
  transition: 0.5s;
}
.main-menu button.close-button {
  display: block;
  width: auto;
  margin: 0;
  height: auto;
  padding: 20px;
  border: none;
  cursor: pointer;
  align-self: flex-end;
  float: right;
  font-size: 2rem;
  background-color: #f9f9f9;
}
.main-menu .popout-container {
  display: flex;
  flex-wrap: wrap;
  height: 70%;
  flex: 1;
  flex-direction: column;
  justify-content: space-around;
  /*Nav Styles*/
}
.main-menu .popout-container ul {
  height: auto;
}
.main-menu .popout-container ul li {
  list-style: none;
}
.main-menu .popout-container ul li a {
  color: rgb(2, 48, 71);
  text-decoration: none;
  display: block;
  border-left: 5px solid #354a95;
  text-transform: uppercase;
  font-size: 2rem;
  transition: all 0.5s ease-out;
  font-weight: bold;
  padding: 10px;
  margin-bottom: 20px;
  min-width: 100%;
}
.main-menu .popout-container ul li a:hover {
  cursor: grab;
  border-left: 2px solid #354a95;
  color: #354a95;
}
.main-menu .popout-container ul li a:active, .main-menu .popout-container ul li a:visited {
  background-color: rgb(2, 48, 71);
}

/*Social Links Styles*/
.social-links {
  margin: auto 0;
  width: auto;
  display: flex;
  justify-content: center;
}
.social-links li {
  list-style: none;
  display: inline-block;
}
.social-links li a {
  color: rgb(2, 48, 71);
  transition: all 0.2s ease;
  height: auto;
  padding: 0 14px;
  font-size: 32px;
}
.social-links li a:hover {
  color: #354a95;
}

#wrapper {
  transition: margin-right 0.5s;
}

/*Section Styles*/
.hero {
  background-color: #ffffff;
}
.hero .hero-content {
  display: block;
  width: 100%;
  max-width: 1440px;
  padding: 80px 40px;
  margin: auto;
}
@media (min-width: 1200px) {
  .hero .hero-content {
    padding: 80px 60px;
  }
}
.hero .hero-content .hero-card-container {
  display: block;
  margin: auto;
}
@media (min-width: 992px) {
  .hero .hero-content .hero-card-container {
    padding: auto;
    display: grid;
    grid-template-rows: 1fr 1fr 1fr;
    grid-template-columns: 1fr 1fr;
  }
}
.hero .hero-content .hero-card-container .card1 {
  grid-row-start: 1;
  grid-column-start: 1;
  grid-row-end: 4;
  grid-column-end: 1;
  padding: 0 60px 0 0;
}
.hero .hero-content .hero-card-container .card1 h2 {
  margin: 0 0 20px 0;
  padding: 0;
  font-size: clamp(16px, 6vw, 56px);
  line-height: 1.3;
}
.hero .hero-content .hero-card-container .card1 p {
  font-size: clamp(16px, 2vw, 24px);
  line-height: 1.8;
  font-weight: 100;
  margin-bottom: 20px;
}
.hero .hero-content .hero-card-container .card2,
.hero .hero-content .hero-card-container .card3,
.hero .hero-content .hero-card-container .card4 {
  border: 1px solid rgb(2, 48, 71);
  margin: 0 auto 20px auto;
  padding: 40px;
}
.hero .hero-content .hero-card-container .card2 h3,
.hero .hero-content .hero-card-container .card3 h3,
.hero .hero-content .hero-card-container .card4 h3 {
  font-size: clamp(16px, 5vw, 24px);
  line-height: 1.4;
}
.hero .hero-content .hero-card-container .card2 p,
.hero .hero-content .hero-card-container .card3 p,
.hero .hero-content .hero-card-container .card4 p {
  font-size: clamp(16px, 3vw, 18px);
  line-height: 1.4;
}
.hero .hero-content .hero-card-container .card2 {
  grid-row-start: 1;
  grid-column-start: 2;
  grid-row-end: 1;
  grid-column-end: 2;
}
.hero .hero-content .hero-card-container .card3 {
  grid-row-start: 2;
  grid-column-start: 2;
  grid-row-end: 2;
  grid-column-end: 2;
}
.hero .hero-content .hero-card-container .card4 {
  grid-row-start: 3;
  grid-column-start: 2;
  grid-row-end: 3;
  grid-column-end: 2;
}

/*Form Styles*/
form {
  position: relative;
}

label,
input,
textarea {
  display: block;
}

input, textarea {
  padding: 10px;
}

textarea {
  width: 100%;
  border-radius: 10px;
}

input {
  padding: 10px 0;
  width: 100%;
  border-radius: 10px;
  border: 1px solid #979797;
}

input.form-button {
  width: auto;
  position: absolute;
  right: 0;
  bottom: -80px;
  padding: 10px 40px;
}

label {
  font-weight: bold;
  text-transform: uppercase;
  font-size: 0.75em;
  margin-bottom: 0.5em;
}

.wpforms-submit-container button.wpforms-submit {
  background-color: rgb(2, 48, 71) !important;
  color: rgb(255, 255, 255) !important;
  border-radius: 38px !important;
  font-weight: bold !important;
  transition: all 0.2s ease;
  letter-spacing: 0.1em !important;
  padding: 12px 24px !important;
}
.wpforms-submit-container button.wpforms-submit:hover {
  background-color: #354a95 !important;
}

/*Layout Styles*/
.page-banner {
  height: auto;
  background-color: rgb(234, 234, 234);
  display: block;
  height: auto;
  padding: 80px 40px;
  text-align: center;
}
@media (min-width: 1200px) {
  .page-banner {
    display: flex;
    flex: 1;
    flex-wrap: wrap;
    justify-content: center;
    justify-items: center;
    width: 100%;
    max-width: 1440px;
    height: 50vh;
    padding: auto 40px;
  }
}
.page-banner h1 {
  font-size: clamp(16px, 6vw, 48px);
  line-height: 1.4;
  margin: auto auto 10px auto;
}
.page-banner p {
  line-height: 1.4;
  margin-bottom: 10px;
}
.page-banner .post-edit-link {
  display: block;
  clear: left;
  padding: 12px 10px 12px 0;
}
@media (min-width: 1200px) {
  .page-banner .post-edit-link {
    text-align: center;
  }
}

.bio-photo {
  width: 250px;
  height: 250px;
  border-radius: 50%;
}

.skip {
  display: none;
}

.button {
  border-radius: 25px;
  color: rgb(255, 255, 255);
  text-transform: uppercase;
  text-decoration: none;
  background-color: #023047;
  padding: 15px 40px;
  display: inline-block;
  font-size: 0.8em;
  transition: all 0.2s ease;
  font-weight: bold;
  letter-spacing: 0.1em;
}
.button:hover {
  background-color: #354a95;
  color: rgb(255, 255, 255);
}

.pill-container {
  margin: 40px auto;
}
@media (min-width: 768px) {
  .pill-container {
    margin: 40px 0;
  }
}

.pill {
  border-radius: 38px;
  color: rgb(255, 255, 255);
  text-transform: uppercase;
  text-decoration: none;
  background-color: rgb(2, 48, 71);
  padding: 12px 24px;
  margin: 0 auto 10px auto;
  display: inline-block;
  width: auto;
  text-align: center;
  font-size: 0.8em;
  transition: all 0.2s ease;
  font-weight: bold;
  letter-spacing: 0.1em;
}
@media (min-width: 576px) {
  .pill {
    display: inline-block;
  }
}
@media (min-width: 768px) {
  .pill {
    padding: 18px 32px;
    width: auto;
    margin: 0 auto 10px auto;
    font-size: 0.8em;
  }
}
@media (min-width: 992px) {
  .pill {
    font-size: 0.8em;
    padding: 12px 24px;
    margin: 0 10px 0 0;
  }
}
.pill:hover {
  background-color: #354a95;
  color: rgb(255, 255, 255);
}

.button-alt {
  background-color: #354a95;
}
.button-alt:hover {
  background-color: rgb(2, 48, 71);
}

.grid-row {
  display: flex;
  display: block;
  width: 100%;
}
.grid-row .grid-container {
  max-width: 1140px;
  justify-items: center;
  padding: 0 40px;
}
@media (min-width: 768px) {
  .grid-row .grid-container {
    display: grid;
    grid-template-rows: 1fr;
    grid-template-columns: 1fr 1fr 1fr;
    margin: auto;
    gap: 20px;
    padding: 0 40px;
  }
}
.grid-row .grid-container .card {
  border: 1px solid rgb(2, 48, 71);
  padding: 20px;
  margin-bottom: 20px;
}
.grid-row .grid-container .card h3 {
  font-size: clamp(16px, 6vw, 28px);
  line-height: 1.4;
}
.grid-row .grid-container .card img {
  width: 100%;
  height: auto;
  border-top-right-radius: 5px;
  border-top-left-radius: 5px;
}
.grid-row .grid-container .card .entry-footer {
  font-size: clamp(14px, 3vw, 16px);
  line-height: 1.4;
  padding: 10px 0;
}

